import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class FollowService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }

	getFollows(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/profiles/' + id + '/follow'

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	followProfile(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/profiles/' + id + '/follow'

		return this.http.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	unfollowProfile(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/profiles/' + id + '/follow'

		return this.http.delete(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

}



