import { Component, OnInit, NgZone, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute, Params, Data } from '@angular/router';
import { DialogService } from "ng2-bootstrap-modal";
import { CloudinaryOptions, CloudinaryUploader } from 'ng2-cloudinary';
import { Title } from '@angular/platform-browser';
import { Observable } from 'rxjs/Rx';

/* Components */
import { MessageModalComponent } from '../modals/message-modal/message-modal.component';
import { PopoverLoginComponent } from '../modals/popover-login/popover-login.component';
// import { GoogleContactModalComponent } from '../modals/google-contact-modal/google-contact-modal.component';
import { LinkedInApiComponent } from '../modals/api-modals/linked-in-api/linked-in-api.component';

/* Services */
import { TokenService } from '../../Services/Authentication/token.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { ProjectsService } from '../../Services/Projects/projects.service';
import { FollowService } from '../../Services/Follows/follow.service';
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { SharedService } from '../../Services/shared.service';
import { SocialService } from '../../Services/Social/social.service';
import { MetaService } from '../../Services/meta.service';
import { AccountService } from '../../Services/account.service';

/* Interfaces */
import { ProfileModels } from '../../Interfaces/profile-models';

/* Animations */
import { profileAnime1, profileAnime2 } from '../../Animations/animations';

@Component({
	selector: 'app-profiles',
	templateUrl: './profiles.component.html',
	styleUrls: [
		'./profiles.component.scss',
		'../../../public/styles/project-card.scss'
	],
	providers: [
		ProfilesService,
		FollowService,
		ProjectsService
	],
	animations: [profileAnime1, profileAnime2]
})
export class ProfilesComponent implements OnInit {

	params 			: any
	my_id 			: boolean = false
	my_ids 			: Object
	profile 		: Object
	follows 		: Object
	projects 		: Array<any> = []
	animationState 	: string
	animateRotate 	: string
	pictureType 	: string
	top 			: number
	uploadingPP 	: boolean = false
	uploadingCP 	: boolean = false
	mobile 			: boolean = false
	modifLocation 	: boolean = false
	reloadChild 	: boolean = false
	ambassador 		: boolean = false

	cloudinaryImage: any;

	uploader: CloudinaryUploader = new CloudinaryUploader(
		new CloudinaryOptions({ cloudName: 'dqpkpmrgk', uploadPreset: 'z7rzegb5', autoUpload: true })
	);

	constructor(private TokenService: TokenService,
				private ProfilesService: ProfilesService,
				private FollowService: FollowService,
				private ProjectsService: ProjectsService,
				private route: ActivatedRoute,
				private router: Router,
				private dialogService: DialogService,
				private PicturesService: PicturesService,
				private SharedService: SharedService,
				private SocialService: SocialService,
				private AccountService: AccountService,
				private ref: ChangeDetectorRef,
				private Title: Title,
				private MetaService: MetaService) {

		this.initWindow();
		this.imageUpload();
	}

	ngOnInit() {
		this.animationState = 'inactive'
		this.animateRotate = 'down'
		this.initParamsRoute();
	}

	initWindow() {
		window.scrollTo(0, 0);

		if ((window.screen.width) < 736)
			this.mobile = true
	}

	// initSEO() {
	// 	this.meta.setTitle(`${this.profile['fullName']} | Wittycircle`);
	// 	this.meta.setTag('og:image', this.profile['cover_picture']);
	// 	this.meta.setTag('og:url', 'https://www.wittycircle.com/' + this.profile['username']);
	// }

	setMetaData() {
		this.Title.setTitle(`${this.profile['fullName']} | Witty`);
		this.MetaService.setMeta('description', `View ${this.profile['fullName']}'s profile on Witty, the professional network for the entrepreneurial age. We connect millions of people to what's being built around them.`);

		this.MetaService.setMeta('og:title', `${this.profile['fullName']}'s profile on Witty`);
		this.MetaService.setMeta('og:description', `View ${this.profile['fullName']}'s profile on Witty, the professional network for the entrepreneurial age. We connect millions of people to what's being built around them.`);
		this.MetaService.setMeta('og:url', 'https://www.wittycircle.com/' + this.profile['username']);
		this.MetaService.setMeta('og:image', this.transformImage(this.profile['picture'], 200, 200, 'fill'));
	}


	imageUpload() {
		this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
			this.cloudinaryImage = JSON.parse(response);
			this.updateProfileImage(this.cloudinaryImage.secure_url);
		};

		this.uploader.onAfterAddingFile = (fileItem) => {
			if (this.pictureType === 'cover')
				this.uploadingCP = true
			else
				this.uploadingPP = true
			this.uploader.uploadAll();
		};
	}

	initParamsRoute() {
		this.route.params.subscribe(params => {
			if (params['username'])
				this.ProfilesService.getIdByUsername(params['username']).subscribe( res => {
					this.my_ids 	= res
					this.profile 	= {}
					this.follows 	= {}
					this.initServices();
					if (this.TokenService.getToken())
						this.my_id 		= this.TokenService.getToken().user.id == this.my_ids['id'] ? true : false
				});
			else
				this.router.navigate(['/']);
		});
	}

	initServices() {
		this.getProfile();
		this.getUserFollows();
		this.getUserProjects();
	}

	getProfile() {
		this.ProfilesService.getProfile(this.my_ids['id']).subscribe( res => {
			this.profile 	= res.profile
			this.top 		= this.getTopRank(this.profile['rank']);
			this.setMetaData();
			this.ref.detectChanges();
		});
	}

	getTopRank(rank) {
		if (rank >= 1 && rank <= 10)
			return 10
		else if (rank >= 11 && rank <= 50 )
			return 50
		else if (rank >= 51 && rank <= 100)
			return 100
		else if (rank >= 101 && rank <= 500)
			return 500

	}

	getUserFollows() {
		this.FollowService.getFollows(this.my_ids['id']).subscribe( res => {
			this.follows = res
		});
	}

	getUserProjects() {
		this.ProjectsService.getUserProjects(this.my_ids['id']).subscribe( res => {
			if (this.my_ids['id'] === 1 || this.my_ids['id'] === 9)
				this.projects.push(res.projects[0]);
			else
				this.projects = res.projects
		});
	}

	//** Modify location **//
	getAddress(place: Object) {
		const address 	= place['formatted_address'].replace('USA', 'United States');
		const location 	= address.split(', ');

		const body = {
			location 		: {
				city 			: location[0],
				state 			: location[1],
				country 		: location[location.length - 1],
				lng 			: place['geometry'].location.lat(),
				lat 			: place['geometry'].location.lng()
			}
		}

		this.updateLocation(body);
	}

	followProfile(id) {
		if (!this.TokenService.getToken())
			return this.showLoginPopOver();

		this.ProfilesService.followProfile(id, {}).subscribe( res => {
			if (!this.profile['hasLiked']) {
				this.profile['follower'] += 1
				this.profile['hasLiked'] = true
			}
			else {
				this.profile['follower'] -= 1
				this.profile['hasLiked'] = false
			}
		});
	}

	setPictureType(type) {
		this.pictureType = type
	}

	getPictureBody(new_picture) {
		if (this.pictureType === 'cover') {
			const body: Object = { cover_picture: new_picture };
			return body
		} else {
			const body: Object = { picture: new_picture };
			return body
		}
	}

	updateProfileImage(new_picture) {
		if (this.my_id) {
			this.uploadingCP = this.uploadingPP = false
			this.ProfilesService.updateProfile(this.profile['id'], this.getPictureBody(new_picture))
				.subscribe( res => {
					if (res.success) {
						if (this.pictureType === 'cover')
							this.profile['cover_picture'] 	= new_picture
						else {
							this.reloadHeader();
							this.profile['picture'] = new_picture
						}
					}
				});
		}
	}

	/** Update Location **/
	updateLocation(body: Object) {
		this.ProfilesService.updateProfile(this.my_ids['id'], body).subscribe( res => {
			if (res.success) {
				this.editLocation();
				this.getProfile();
			}
		});
	}

	toggleProjects() {
		this.animationState = this.animationState === 'active' ? 'inactive' : 'active'
		this.animateRotate 	= this.animateRotate === 'down' ? 'up' : 'down'
	}

	reloadHeader() {
		this.SharedService.reloadHeader(true);
	}

	editLocation() {
		this.modifLocation = this.modifLocation ? false : true
		this.ref.detectChanges();
	}

	/* TRANSFORM */
	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	/* SHOW MODAL */
	showSearchUserModal(id: number) {
		if (!this.TokenService.getToken())
			return this.showLoginPopOver();
		else {
			const my_id = this.TokenService.getToken().user.id
	  		this.AccountService.getSocialInvite(my_id).subscribe( check => {
				this.ProfilesService.getProfile(my_id).subscribe( res => {
					// if (res.profile.rank <= 2500 || check.invite_google) {
						let disposable 	= this.dialogService.addDialog(MessageModalComponent, {
			            	profile 	: this.profile
				    	});
					// } else {
					// 	this.openModalForSocial(1);
					// }
				});
			});
	    }
	}

	openLinkedInApiModal() {
		let disposable 	= this.dialogService.addDialog(LinkedInApiComponent, {
        	profile 	: this.profile,
        	index 		: 0
    	}).subscribe( success => {
    		this.reloadChild = success;
    		this.getProfile();
    	});
	}

	showLoginPopOver() {
        let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {});
	}

}



