import { Component, Input, OnInit, ViewEncapsulation, OnChanges, SimpleChanges } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";

/* Components */
import { ProfileSkillComponent } from '../profile-modals/profile-skill/profile-skill.component';
import { ProfileInterestComponent } from '../profile-modals/profile-interest/profile-interest.component';
import { ProfileAboutComponent } from '../profile-modals/profile-about/profile-about.component';
import { ExperienceModalComponent } from '../../modals/experience-modal/experience-modal.component';

/* Services */
import { SkillsService } from '../../../Services/Skills/skills.service';
import { InterestsService } from '../../../Services/Interests/interests.service';
import { ExperiencesService } from '../../../Services/Experiences/experiences.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';

/* Interfaces */
import { SearchModel } from '../../../Interfaces/search-models';
import { searchProfile3 } from '../../../Interfaces/Constants/search-profile.constant';

@Component({
	selector: 'app-profile-info',
	templateUrl: './profile-info.component.html',
	styleUrls: ['./profile-info.component.scss'],
	encapsulation: ViewEncapsulation.None,
	providers: [SkillsService, InterestsService, ExperiencesService]
})
export class ProfileInfoComponent implements OnInit, OnChanges {

	@Input() mobile 		: boolean
	@Input() about 			: Object
	@Input() description	: string
	@Input() id 			: number
	@Input() my_id 			: boolean
	@Input() uid 			: number
	@Input() reloadChild 	: boolean

	skills 			: Object[]
	skillList 		: Object[]
	interestList	: any
	interests		: Array<string>
	experiences 	: Object[]
	searchBody 		: SearchModel = searchProfile3
	similarProfiles : Object[]

	constructor(private SkillsService: SkillsService,
				private InterestsService: InterestsService,
				private ExperiencesService: ExperiencesService,
				private dialogService: DialogService,
				private ProfilesService: ProfilesService){}

	ngOnInit() {
		this.initServices();
	}

	ngOnChanges(changes: SimpleChanges) {
		this.initServices();
	}

	initServices() {
		this.getUserSkills();
		this.getUserInterests();
		this.getUserExperiences();
		this.searchSimilarProfiles();
	}

	getUserSkills() {
		this.skills 		= this.about['skills'].split(',');
	}

	getUserInterests() {
		this.InterestsService.getUserInterests(this.uid).subscribe( res => {
			this.interests = res.interests
		});
	}

	getUserExperiences() {
		this.ExperiencesService.getUserExperiences(this.uid).subscribe( res => {
			this.experiences = res.experiences
		});
	}

	removeUserExperience(id, index) {
		if (this.my_id) {
			this.ExperiencesService.deleteExperience(this.uid, {id: id})
			.subscribe( res => {
				if (res.success)
					this.experiences.splice(index, 1);
			});
		}
	}

	searchSimilarProfiles() {

		this.searchBody.query.members[0].value 		= this.skills.slice(0, 3);
		this.searchBody.query.members[1].value 		= this.about['about']
		if (this.about['city']) {
			const location = this.about['city'] + ', ' + (this.about['state'] || ' ') + ', ' + this.about['country']
			this.searchBody.query.members[2].value 	= location
		}
		if (this.about['name'])
			this.searchBody.query.members[3].value 	= this.about['name']
		this.searchBody.paginate.limit 				= 5
		this.searchBody.query.priority 				= 'skills'

		this.ProfilesService.searchProfiles(this.searchBody).subscribe( res => {
			this.similarProfiles = res.profiles
		});
	}

	/* SHOW MODAL */
	showSkillModal() {
		let disposable 	= this.dialogService.addDialog(ProfileSkillComponent, {
			user_id : this.about['uid']
		}).subscribe((skills: Array<string>) => {
			console.log(skills);
		});
	}

    showInterestModal() {
    	if (this.my_id) {
	        let disposable = this.dialogService.addDialog(ProfileInterestComponent, {
	            user_interests	: this.interests
	        })
	    }
    }

    showAboutModal() {
    	if (this.my_id) {
	        let disposable = this.dialogService.addDialog(ProfileAboutComponent, {
	            profile_id		: this.id,
	            profile_about 	: this.description,
	            profile_status 	: this.about['about']
	        }).subscribe((about) => {
	        	if (about) {
	        		this.about['about'] = about['status'];
	        		this.description	= about['description'];
	        	}
	        })
	    }
    }

    showExperienceModal(index) {
    	if (this.my_id) {
    		const object = index < 0 ? {} : this.experiences[index]
	        let disposable = this.dialogService.addDialog(ExperienceModalComponent, {
	        	index 			 	: index,
	            user_experience 	: object
	        })
	        .subscribe((success) => {
	        	if (success)
	        		this.getUserExperiences();
	        })
	    }
    }
}



