import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';

/* Components */
import { ProfilesComponent } from './profiles.component';
import { ProfileInfoComponent } from './profile-info/profile-info.component';
import { ProfileSkillComponent } from './profile-modals/profile-skill/profile-skill.component';
import { ProfileAboutComponent } from './profile-modals/profile-about/profile-about.component';
import { ProfileInterestComponent } from './profile-modals/profile-interest/profile-interest.component';
import { ExperienceModalComponent } from '../modals/experience-modal/experience-modal.component';

/* Services */
import { AuthenticationService } from '../../Services/Authentication/authentication.service';
import { TokenService } from '../../Services/Authentication/token.service';
import { SocialService } from '../../Services/Social/social.service';
import { AccountService } from '../../Services/account.service';

/* Directives */
import { ExperienceModalDirective } from '../../Directives/Modals/experience.modal.directive';
import { DirectivesModule } from '../../Directives/directives.module';

/* Librairies */
import { MomentModule } from 'angular2-moment';
import { Ng2CloudinaryModule } from 'ng2-cloudinary';
import { FileUploadModule } from 'ng2-file-upload';

/* Route */
import { PROFILE_ROUTES } from '../../Routes/profiles.routes';

@NgModule({
	imports: [
		PROFILE_ROUTES,
		CommonModule,
		RouterModule,
		FormsModule,
		MomentModule,
		PipeModule,
		FooterModule,
		DirectivesModule,
		Ng2CloudinaryModule,
		FileUploadModule
	],
	exports: [
	],
	declarations: [
		ProfilesComponent,
		ProfileInfoComponent,
		ExperienceModalDirective
	],
	providers: [
		AuthenticationService,
		TokenService,
		SocialService,
		AccountService
	]
})
export class ProfilesModule { }



